@use "global" as *;

.p-service {
  padding-top: rem(40);
  padding-bottom: rem(80);
  @include mq(md) {
    padding-top: rem(60);
    padding-bottom: rem(110);
  }
}

.p-service__contents {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: rem(50);
  @include mq(md) {
    row-gap: rem(60);
  }
}

.p-service__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: rem(615);
  @include mq(md) {
    flex-direction: row;
    max-width: 100%;
  }
}

.p-service__item-img {
  width: 100%;
  position: relative;
  overflow: hidden;
}

.p-service__item-img::before {
  content: "";
  display: block;
  padding-top: calc(211 / 335 * 100%); // 縦 ÷ 横 × １００
  @include mq("md") {
    padding-top: calc(280 / 445 * 100%); // 縦 ÷ 横 × １００
  }
}

.p-service__item-img img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}

.p-service__item-content {
  max-width: rem(615);
  margin-top: rem(20);
  @include mq(md) {
    margin-top: 0;
    margin-left: rem(50);
  }
}

.p-service__item-title {
  font-size: rem(18);
  font-weight: bold;
  line-height: calc(22 / 18);
  color: $black;
  text-align: center;
  @include mq(md) {
    font-size: rem(24);
    line-height: calc(29 / 24);
    text-align: left;
  }
}

.p-service__item-text {
  font-size: rem(14);
  line-height: rem(24);
  color: $black;
  margin-top: rem(15);
  @include mq(md) {
    font-size: rem(15);
    line-height: rem(27);
    margin-top: rem(20);
  }
}
