@use "global" as *;

// .p-job-card {}

.p-job-card__title {
  font-size: rem(24);
  line-height: 1;
  font-weight: bold;
  color: $black;
  text-align: center;
  @include mq(md) {
    font-size: rem(32);
  }
}

.p-job-card__content {
  background-color: $white;
  border: 1px solid #aeaeae;
  border-radius: rem(10);
  padding: rem(30) 0;
  margin-top: rem(30);
  @include mq() {
    padding: rem(60) 0;
    margin-top: rem(40);
  }
}

.p-job-card__content-inner {
  width: 100%;
  padding-right: rem(strip-unit($padding-sp));
  padding-left: rem(strip-unit($padding-sp));
  margin-right: auto;
  margin-left: auto;
  @include mq(md) {
    max-width: calc($innerWidth + rem(190));
    padding-right: rem(95);
    padding-left: rem(95);
  }
}

.p-job-card__content-title {
  font-size: rem(16);
  font-weight: bold;
  line-height: calc(19 / 16);
  color: $black;
  text-align: center;
  @include mq(md) {
    font-size: rem(24);
    line-height: calc(29 / 24);
  }
}

.p-job-card__content-item {
  display: flex;
  flex-direction: column;
  align-items: baseline;
  color: $black;
  border-bottom: 1px solid #dddddd;
  padding: rem(20) 0;
  @include mq() {
    flex-direction: row;
    padding: rem(30) 0;
  }
}

.p-job-card__content-item dt {
  width: 100%;
  font-family: $font-sub;
  font-size: rem(16);
  line-height: 1;
  font-weight: 600;
  @include mq() {
    max-width: calc(#{rem(175)} + #{rem(30)});
    padding-left: rem(30);
  }
}

.p-job-card__content-item dd {
  width: 100%;
  font-size: rem(16);
  font-weight: 400;
  line-height: calc(19 / 16);
  margin-top: rem(10);
  @include mq(md) {
    margin-top: 0;
    padding-right: rem(30);
  }
}

.p-job-card__btn {
  text-align: center;
  margin-top: rem(40);
  @include mq(md) {
    margin-top: rem(50);
  }
}

.p-job-card__btn span {
  display: block;
  font-size: rem(12);
  line-height: 1;
  font-weight: 400;
  color: $black;
  margin-top: rem(5);
}
