@use "global" as *;

.js-scroll-top {
  transition: opacity 1s;
  visibility: hidden;
  opacity: 0;
}

.js-scroll-top.is-fadein {
  visibility: visible;
  opacity: 1;
}

.p-arrow {
  background-color: $white;
  border: 1px solid #707070;
  border-radius: 50%;
  cursor: pointer;
  padding: rem(24);
  transition: all 0.3s ease-in-out;
  position: fixed;
  right: rem(45);
  bottom: rem(45);
  z-index: 1;
}

.p-arrow:hover {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
