@use "global" as *;

.c-card {
  display: block;
}

.c-card__img {
  width: 100%;
  position: relative;
  overflow: hidden;
}

.c-card__img::before {
  content: "";
  display: block;
  padding-top: calc(210 / 335 * 100%);
  @include mq(md) {
    padding-top: calc(220 / 350 * 100%);
  }
}

.c-card__img img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}

.c-card__title {
  font-size: rem(18);
  font-weight: bold;
  line-height: calc(22 / 18);
  text-align: center;
  color: $white;
  margin-top: rem(14);
  @include mq(md) {
    margin-top: rem(25);
  }
}

.c-card__text {
  font-family: $font-sub;
  font-size: rem(15);
  font-weight: 300;
  line-height: rem(27);
  color: $white;
  margin-top: rem(10);
  @include mq(md) {
    margin-top: rem(20);
  }
}
