@use "global" as *;

.p-company-parts {
  background-color: $black;
}

.p-company-parts__outside-inner {
  width: 100%;
  max-width: rem(1440);
  padding-top: rem(40);
  padding-bottom: rem(80);
  margin-right: auto;
  margin-left: auto;
  position: relative;
  @include mq(md) {
    padding-top: rem(140);
  }
}

.p-company-parts__contents {
  display: flex;
  justify-content: center;
  @include mq(md) {
    justify-content: space-between;
  }
}

.p-company-parts__inner {
  width: 100%;
  padding-right: rem(strip-unit($padding-sp));
  padding-left: rem(strip-unit($padding-sp));
  margin-right: auto;
  margin-left: auto;
  @include mq(md) {
    max-width: 1300px + $padding-pc * 2;
    padding-right: rem(strip-unit($padding-pc));
    padding-left: rem(strip-unit($padding-pc));
  }
}

.p-company-parts__left {
  flex-grow: 1;
  text-align: right;
}

.p-company-parts__left .p-company-parts__img-s {
  display: inline-block;
}

.p-company-parts__right {
  flex-grow: 1;
  text-align: right;
}

.p-company-parts__right .p-company-parts__img-l {
  display: inline-block;
}

.p-company-parts__img-l {
  width: rem(210);
}

.p-company-parts__img-s {
  width: rem(160);
  margin-top: rem(45);
}

.p-company-parts__content {
  text-align: center;
  padding: 0 rem(25);
  position: relative;
  z-index: 1;
}

.p-company-parts__content-title {
  font-size: rem(24);
  line-height: 1;
  font-weight: bold;
  color: $white;
  @include mq(md) {
    font-size: rem(32);
  }
}

.p-company-parts__catch-copy {
  margin-top: rem(30);
  @include mq(md) {
    margin-top: rem(40);
  }
}

.p-company-parts__catch-copy h3 {
  display: inline;
  font-family: $font-sub;
  font-size: rem(32);
  font-weight: 600;
  line-height: rem(48);
  color: $white;
  background-color: #a1a1a1;
  background: linear-gradient(
    to top,
    transparent 0%,
    #a1a1a1 0%,
    #a1a1a1 20%,
    transparent 20%,
    transparent 100%
  );
  @include mq(md) {
    font-size: rem(40);
  }
}

.p-company-parts__text {
  margin-top: rem(40);
  @include mq(md) {
    margin-top: rem(45);
  }
}

.p-company-parts__text {
  font-family: $font-sub;
  font-size: rem(14);
  font-weight: 300;
  line-height: rem(25);
  color: $white;
  @include mq(md) {
    font-size: rem(15);
    line-height: rem(27);
  }
}

.p-company-parts__btn {
  margin-top: rem(30);
}

.p-company-parts__dot1 {
  width: rem(50);
  height: rem(50);
  background-image: radial-gradient(#f9fdc8 70%, transparent 70%);
  background-size: rem(50) rem(50);
  position: absolute;
  top: 24%;
  left: 0%;
  @include mq(md) {
    top: 23.5%;
    left: 21.5%;
  }
}

.p-company-parts__dot2 {
  width: rem(95);
  height: rem(95);
  background-image: radial-gradient(#f4f2f8 70%, transparent 70%);
  background-size: rem(95) rem(95);
  position: absolute;
  top: 0%;
  left: 6%;
  @include mq(md) {
    width: rem(180);
    height: rem(180);
    background-size: rem(180) rem(180);
    top: 0%;
    left: 29%;
  }
}

.p-company-parts__dot3 {
  width: rem(65);
  height: rem(65);
  background-image: radial-gradient(#e8faff 70%, transparent 70%);
  background-size: rem(65) rem(65);
  position: absolute;
  top: 9%;
  right: 20%;
  @include mq(md) {
    width: rem(90);
    height: rem(90);
    background-size: rem(90) rem(90);
    top: 12.5%;
    right: 30%;
  }
}

.p-company-parts__dot4 {
  width: rem(50);
  height: rem(50);
  background-image: radial-gradient(#fbedff 70%, transparent 70%);
  background-size: rem(50) rem(50);
  position: absolute;
  top: 50%;
  right: 0%;
  @include mq(md) {
    width: rem(90);
    height: rem(90);
    background-size: rem(90) rem(90);
    top: 1.5%;
    right: 18.5%;
  }
}

.p-company-parts__dot5 {
  width: rem(50);
  height: rem(50);
  background-image: radial-gradient(#fbedff 70%, transparent 70%);
  background-size: rem(50) rem(50);
  position: absolute;
  bottom: 33%;
  left: 0%;
  @include mq(md) {
    bottom: 14%;
    left: 31%;
  }
}

.p-company-parts__dot6 {
  width: rem(50);
  height: rem(50);
  background-image: radial-gradient(#f9fdc8 70%, transparent 70%);
  background-size: rem(50) rem(50);
  position: absolute;
  bottom: 0%;
  right: 2.5%;
  @include mq(md) {
    bottom: 0%;
    right: 32%;
  }
}

.p-company-parts__dot7 {
  width: rem(50);
  height: rem(50);
  background-image: radial-gradient(#e8faff 70%, transparent 70%);
  background-size: rem(50) rem(50);
  position: absolute;
  bottom: 9.5%;
  left: 24%;
  @include mq(md) {
    top: 0;
    left: 0;
  }
}
