@use "global" as *;

.p-header {
  display: flex;
  justify-content: space-between;
  height: rem(64);
  background-color: transparent;
  padding-left: rem(22);
  padding-right: rem(18);
  transition: 0.3s;
  @include mq("md") {
    height: rem(86);
  }
}

.p-header.color-change {
  background-color: #141414;
}

.p-header__nav {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  // z-index: 50;
  z-index: 1;
  @include mq(md) {
    display: block;
    position: static;
  }
}

.p-header__logo {
  display: flex;
  align-items: center;
  height: inherit;
  font-size: rem(18);
  line-height: 1;
  color: $white;
}

.p-header__hamburger-btn {
  position: absolute;
  top: 0;
  right: 0;
  // z-index: 100;
  z-index: 2;
  height: inherit;
  width: rem(64);
  @include mq(md) {
    display: none;
  }
}

.p-header__hamburger-btn span {
  width: rem(24);
  height: rem(2);
  background-color: $white;
  display: block;
  border-radius: rem(2);
  margin-left: auto;
  margin-right: auto;
  position: relative;
  transition: all 0.3s;
}

.p-header__hamburger-btn span:nth-child(2) {
  margin-top: rem(4);
  margin-bottom: rem(4);
}

.p-header__hamburger-btn.open span:nth-child(1) {
  transform: rotate(45deg);
  top: rem(5);
}
.p-header__hamburger-btn.open span:nth-child(2) {
  opacity: 0;
}
.p-header__hamburger-btn.open span:nth-child(3) {
  transform: rotate(-45deg);
  top: rem(-7);
}
