@use "global" as *;

.c-section-title {
  display: inline-block;
  font-size: rem(14);
  line-height: 1;
  color: $black;
  text-align: center;
}

.c-section-title::before {
  content: attr(data-en);
  display: block;
  font-size: rem(36);
  line-height: calc(44 / 36);
  text-transform: uppercase;
  margin-bottom: rem(5);
}

.c-section-title.c-section-title--left {
  text-align: left;
}

.c-section-title.c-section-title--white {
  color: $white;
}
