@use "global" as *;

.p-sub-mv {
  width: 100%;
  height: rem(190);
  background-image: url(../images/sub_mv.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding-top: rem(64);
  position: relative;
  overflow-x: hidden;
  @include mq(md) {
    height: rem(240);
    padding-top: rem(86);
  }
}

.p-sub-mv__title {
  width: 100%;
  text-align: center;
  padding-right: rem(30);
  padding-left: rem(30);
  position: absolute;
  top: 59.887%;
  transform: translateY(-50%);
}
