@use "global" as *;

.c-card2 {
  display: block;
}

.c-card2__img {
  width: 100%;
  position: relative;
  overflow: hidden;
}

.c-card2__img::before {
  content: "";
  display: block;
  padding-top: calc(234 / 345 * 100%); // 縦 ÷ 横 × １００
  @include mq("md") {
    padding-top: calc(238 / 350 * 100%); // 縦 ÷ 横 × １００
  }
}

.c-card2__img img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}

.c-card2__title {
  font-size: rem(16);
  line-height: calc(19 / 16);
  font-weight: 400;
  color: $black;
  text-align: center;
  margin-top: rem(15);
}
