@use "global" as *;

.p-mv {
  width: 100%;
  height: 100vh;
  background-image: url(../images/top_mv.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding-top: rem(64);
  position: relative;
  overflow-x: hidden;
  @include mq(md) {
    padding-top: rem(86);
  }
}

.p-mv__contents {
  width: 100%;
  padding-right: rem(30);
  padding-left: rem(30);
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.p-mv__message {
  color: $white;
  @include mq(md) {
    text-align: center;
  }
}

.p-mv__en-message h2 {
  font-weight: bold;
  font-size: rem(40);
  line-height: rem(44);
  @include mq(md) {
    font-size: rem(72);
    line-height: calc(88 / 72);
  }
}
.p-mv__jp-message {
  margin-top: rem(20);
}

.p-mv__jp-message h2 {
  font-family: $font-sub;
  font-weight: 600;
  font-size: rem(16);
  line-height: rem(29);
  @include mq(md) {
    font-size: rem(24);
    line-height: 1;
  }
}

.p-mv__btn {
  display: block;
  margin-top: rem(30);
  @include mq(md) {
    text-align: center;
    margin-top: rem(50);
  }
}

.p-mv__btn a {
  display: inline-block;
  min-width: rem(220);
  font-size: rem(15);
  line-height: calc(19 / 15);
  color: $white;
  background-color: transparent;
  border: 1px solid $white;
  border-radius: rem(30);
  text-align: center;
  text-transform: uppercase;
  transition: 0.3s;
  padding: rem(18) 0;
}

.p-mv__btn a:hover {
  color: $black;
  background-color: $white;
}
