@use "global" as *;

.p-news {
  padding-top: rem(40);
  padding-bottom: rem(80);
  @include mq(md) {
    padding-top: rem(60);
    padding-bottom: rem(110);
  }
}

.p-news__inner {
  width: 100%;
  padding-right: rem(strip-unit($padding-sp));
  padding-left: rem(strip-unit($padding-sp));
  margin-right: auto;
  margin-left: auto;
  @include mq(md) {
    max-width: 915px + $padding-pc * 2;
    padding-right: rem(strip-unit($padding-pc));
    padding-left: rem(strip-unit($padding-pc));
  }
}

.p-news__contents {
  max-width: rem(500);
  margin: 0 auto;
  @include mq(md) {
    max-width: 100%;
  }
}
