@use "global" as *;

// .p-recruit {}

.p-recruit__bg {
  padding-top: rem(40);
  padding-bottom: rem(80);
  @include mq(md) {
    background-image: url(../images/bg-geometry.jpg);
    background-size: rem(400);
    padding-top: rem(60);
    padding-bottom: rem(110);
  }
}

.p-recruit__inner {
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  position: relative;
  z-index: 1;
  @include mq(md) {
    max-width: rem(1440);
  }
}

.p-recruit__catch {
  background-color: transparent;
  background-size: cover;
  background-position: center;
  clip-path: polygon(0 0, 100% 0, 100% calc(100% - #{rem(40)}), 0 100%);
  padding-bottom: rem(40);
  padding-right: rem(20);
  padding-left: rem(20);
  text-align: center;
}

.p-recruit__catch-title {
  display: inline-block;
  font-family: $font-sub;
  font-size: rem(18);
  line-height: 1;
  font-weight: 300;
  color: $black;
  @include mq(md) {
    font-size: rem(32);
  }
}

.p-recruit__catch-title::before {
  content: attr(data-en);
  display: block;
  font-family: $font-main;
  font-size: rem(48);
  line-height: calc(56 / 48);
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: rem(15);
  @include mq(md) {
    font-size: rem(72);
    line-height: calc(88 / 72);
  }
}

.p-recruit__catch-text {
  font-size: rem(15);
  line-height: calc(27 / 15);
  font-weight: 300;
  color: $black;
  @include mq(md) {
    font-size: rem(16);
    line-height: calc(28 / 16);
  }
}

.p-recruit__bg-img {
  min-height: calc(#{rem(210)} + #{rem(40)} + #{rem(40)});
  background-image: url(../images/recruit_bg_gray.jpg);
  background-size: cover;
  background-position: center;
  clip-path: polygon(0 rem(40), 100% 0, 100% calc(100% - #{rem(40)}), 0 100%);
  padding-top: rem(40);
  padding-bottom: rem(40);
  @include mq(md) {
    min-height: calc(#{rem(510)} + #{rem(40)} + #{rem(40)});
  }
}

.p-recruit__Job-card {
  width: 100%;
  padding-right: rem(strip-unit($padding-sp));
  padding-left: rem(strip-unit($padding-sp));
  margin-top: rem(40);
  margin-right: auto;
  margin-left: auto;
  @include mq(md) {
    max-width: $innerWidth + $padding-pc * 2;
    padding-right: rem(strip-unit($padding-pc));
    padding-left: rem(strip-unit($padding-pc));
    margin-top: rem(70);
  }
}

.p-recruit__movie {
  width: 100%;
  padding-right: rem(strip-unit($padding-sp));
  padding-left: rem(strip-unit($padding-sp));
  margin-top: rem(80);
  margin-right: auto;
  margin-left: auto;
  @include mq(md) {
    max-width: $innerWidth + $padding-pc * 2;
    padding-right: rem(strip-unit($padding-pc));
    padding-left: rem(strip-unit($padding-pc));
    margin-top: rem(140);
  }
}

.p-recruit__movie-title {
  font-family: $font-sub;
  font-size: rem(24);
  line-height: 1;
  font-weight: 700;
  color: $black;
  text-align: center;
  @include mq(md) {
    font-size: rem(32);
  }
}

.p-recruit__movie-content {
  width: 100%;
  height: 100%;
  aspect-ratio: 335 / 188;
  margin-top: rem(30);
  @include mq(md) {
    aspect-ratio: 1110 / 624;
    margin-top: rem(40);
  }
}
