@use "global" as *;

.p-top-works {
  background: linear-gradient(
    180deg,
    transparent 0%,
    transparent 24%,
    #f7f7f7 24%,
    #f7f7f7 100%
  );
  padding-bottom: rem(60);
  @include mq(md) {
    background: transparent;
    padding-bottom: 0;
  }
}

.p-top-works__inner {
  width: 100%;
  padding-right: rem(strip-unit($padding-sp));
  padding-left: rem(strip-unit($padding-sp));
  margin-right: auto;
  margin-left: auto;
  @include mq(md) {
    max-width: 1260px + $padding-pc * 2;
    padding-right: rem(strip-unit($padding-pc));
    padding-left: rem(strip-unit($padding-pc));
  }
}

.p-top-works__body {
  @include mq(md) {
    background: linear-gradient(
      90deg,
      transparent 0%,
      transparent 34%,
      #d7d7d7 34%,
      #d7d7d7 100%
    );
    padding-top: rem(60);
    padding-bottom: rem(60);
  }
}

.p-top-works__contents {
  display: flex;
  flex-direction: column;
  max-width: rem(500);
  margin-right: auto;
  margin-left: auto;
  position: relative;
  @include mq(md) {
    flex-direction: row;
    align-items: center;
    max-width: 100%;
    margin-right: 0;
    margin-left: 0;
  }
}

.p-top-works__content {
  width: 100%;
  max-width: rem(430);
  margin-top: rem(25);
  margin-right: auto;
  margin-left: auto;
  @include mq(md) {
    margin-top: 0;
    margin-right: 0;
    margin-left: rem(20);
  }
}
