@use "global" as *;

.p-top-service {
  background-color: $black;
  padding: rem(30);
  @include mq(md) {
    padding: rem(60);
  }
}

.p-top-service__title {
  text-align: center;
}

.p-top-service__cards {
  margin-top: rem(30);
  max-width: rem(500);
  margin-left: auto;
  margin-right: auto;
  @include mq(md) {
    max-width: 100%;
  }
}

.p-top-service__btn {
  text-align: center;
  margin-top: rem(30);
}
