@use "global" as *;

.p-swiper {
  width: 100%;
}

.p-swiper__item {
  width: 100%;
}

.p-swiper__img {
  width: 100%;
  position: relative;
}

.p-swiper__img::before {
  content: "";
  display: block;
  padding-top: calc(230 / 335 * 100%);
  @include mq(md) {
    padding-top: calc(510 / 750 * 100%);
  }
}

.p-swiper__img img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}

// ページネーションをスライダーの外に出す
.swiper-pagination-bullets {
  text-align: right;
  position: static;
  margin-top: rem(10);
  @include mq() {
    margin-top: rem(15);
  }
}

.swiper-pagination-bullet {
  background: $black;
}
