@use "global" as *;

.p-single-news {
  padding-top: rem(40);
  padding-bottom: rem(80);
  @include mq(md) {
    padding-top: rem(60);
    padding-bottom: rem(110);
  }
}

.p-single-news__inner {
  width: 100%;
  padding-right: rem(strip-unit($padding-sp));
  padding-left: rem(strip-unit($padding-sp));
  margin-right: auto;
  margin-left: auto;
  @include mq(md) {
    max-width: 751px + $padding-pc * 2;
    padding-right: rem(strip-unit($padding-pc));
    padding-left: rem(strip-unit($padding-pc));
  }
}

.p-single-news__contents {
}

.p-single-news__title {
  font-size: rem(22);
  line-height: rem(34);
  font-weight: normal;
  color: $black;
  @include mq(md) {
    font-size: rem(32);
    line-height: rem(46);
  }
}

.p-single-news__meta {
  display: flex;
  align-items: center;
  margin-top: rem(20);
  @include mq(md) {
    margin-top: rem(10);
  }
}

.p-single-news__date {
  font-size: rem(13);
  line-height: calc(16 / 13);
  color: $black;
}

.p-single-news__category {
  min-width: rem(82);
  font-weight: bold;
  font-size: rem(12);
  line-height: 1;
  color: $black;
  background-color: #e8e8e8;
  border-radius: rem(12);
  text-align: center;
  padding: rem(5) 0;
  margin-left: rem(15);
  @include mq() {
    margin-left: rem(20);
  }
}

.p-single-news__img {
  width: 100%;
  margin-top: rem(20);
  position: relative;
  overflow: hidden;
  @include mq(md) {
    margin-top: rem(30);
  }
}

.p-single-news__img::before {
  content: "";
  display: block;
  padding-top: calc(187 / 335 * 100%);
  @include mq(md) {
    padding-top: calc(400 / 750 * 100%);
  }
}

.p-single-news__img img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}

.p-single-news__img-text {
  font-family: $font-sub;
  font-size: rem(14);
  font-weight: 300;
  line-height: calc(24 / 14);
  color: $black;
  margin-top: rem(30);
  @include mq(md) {
    font-size: rem(15);
    line-height: calc(25 / 15);
    margin-top: rem(30);
  }
}

.p-single-news__sub-contents-l {
  font-family: $font-sub;
  margin-top: rem(40);
  @include mq(md) {
    margin-top: rem(60);
  }
}

.p-single-news__sub-contents-l h3 {
  display: flex; /* 文字と線を横並び */
  align-items: center; /* 線を上下中央 */
  font-size: rem(18);
  font-weight: 600;
  line-height: calc(21 / 18);
  color: $black;
  @include mq(md) {
    font-size: rem(24);
    line-height: 1;
  }
}

.p-single-news__sub-contents-l h3::before {
  content: "";
  display: inline-block;
  width: rem(18);
  height: rem(6);
  background-color: $black;
  border-radius: rem(5);
  transform: rotate(90deg);
  margin-left: rem(-6);
  @include mq(md) {
    width: rem(38);
    margin-left: rem(-16);
  }
}

.p-single-news__sub-contents-l p {
  font-size: rem(14);
  font-weight: 300;
  line-height: calc(24 / 14);
  color: $black;
  margin-top: rem(20);
  @include mq(md) {
    font-size: rem(15);
    line-height: calc(25 / 15);
  }
}

.p-single-news__sub-contents-s {
  font-family: $font-sub;
  margin-top: rem(40);
}

.p-single-news__sub-contents-s h4 {
  font-size: rem(16);
  font-weight: 600;
  line-height: calc(21 / 16);
  color: $black;
  @include mq(md) {
    font-size: rem(18);
    line-height: calc(21 / 18);
  }
}

.p-single-news__sub-contents-s p {
  font-size: rem(14);
  font-weight: 300;
  line-height: calc(24 / 14);
  color: $black;
  margin-top: rem(20);
  @include mq(md) {
    font-size: rem(15);
    line-height: calc(25 / 15);
  }
}
