@use "global" as *;

.c-sub-title {
  display: inline-block;
  font-size: rem(14);
  line-height: 1;
  color: $white;
  text-align: center;
}

.c-sub-title::before {
  content: attr(data-en);
  display: block;
  font-size: rem(36);
  line-height: calc(44 / 36);
  text-transform: uppercase;
  margin-bottom: rem(5);
}
