@use "global" as *;

.p-news-item {
  list-style: none;
}

.p-news-item:not(:first-child) {
  margin-top: rem(40);
  @include mq(md) {
    margin-top: rem(15);
  }
}

.p-news-item__link {
  display: block;
  @include mq(md) {
    display: flex;
    align-items: baseline;
  }
}

.p-news-item__meta {
  display: flex;
  align-items: center;
}

.p-news-item__date {
  font-size: rem(15);
  line-height: calc(19 / 15);
  color: $black;
}

.p-news-item__category {
  min-width: rem(82);
  font-weight: bold;
  font-size: rem(12);
  line-height: 1;
  color: $black;
  background-color: #e8e8e8;
  border-radius: rem(12);
  text-align: center;
  padding: rem(5) 0;
  margin-left: rem(15);
  @include mq() {
    margin-left: rem(20);
  }
}

.p-news-item__title {
  font-family: $font-sub;
  font-size: rem(15);
  line-height: rem(27);
  color: $black;
  margin-top: rem(10);
  @include mq() {
    margin-top: 0;
    margin-left: rem(20);
  }
}
