@use "global" as *;

.p-company {
  padding-top: rem(40);
  padding-bottom: rem(80);
  @include mq(md) {
    padding-top: rem(60);
    padding-bottom: rem(110);
  }
}

.p-company__inner {
  width: 100%;
  padding-right: rem(strip-unit($padding-sp));
  padding-left: rem(strip-unit($padding-sp));
  margin-right: auto;
  margin-left: auto;
  @include mq(md) {
    max-width: $innerWidth + $padding-pc * 2;
    padding-right: rem(strip-unit($padding-pc));
    padding-left: rem(strip-unit($padding-pc));
  }
}

.p-company__ceo {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: rem(500);
  margin-top: rem(30);
  margin-right: auto;
  margin-left: auto;
  @include mq(md) {
    flex-direction: row;
    max-width: 100%;
    margin-top: rem(60);
    margin-right: 0;
    margin-left: 0;
  }
}

.p-company__ceo-img img {
  aspect-ratio: 335 / 248; //横幅÷縦幅
  object-fit: cover;
  @include mq(md) {
    aspect-ratio: 445 / 330; //横幅÷縦幅
  }
}

.p-company__ceo-text {
  font-family: $font-sub;
  font-size: rem(14);
  line-height: calc(24 / 14);
  font-weight: 300;
  color: $black;
  margin-top: rem(30);
  @include mq(md) {
    max-width: rem(605);
    font-size: rem(15);
    line-height: calc(25 / 15);
    margin-top: 0;
    margin-left: rem(50);
  }
}

.p-company__info {
  margin-top: rem(80);
  @include mq(md) {
    margin-top: rem(140);
  }
}

.p-company__map {
  width: 100%;
  margin-top: rem(30);
}

.p-company__map iframe {
  aspect-ratio: 335 / 240;
  width: 100%;
  height: 100%;
  -webkit-filter: grayscale(100%);
  -moz-filter: grayscale(100%);
  -ms-filter: grayscale(100%);
  -o-filter: grayscale(100%);
  filter: grayscale(100%);
  @include mq(md) {
    aspect-ratio: 1110 / 394;
  }
}
