@use "global" as *;

.p-header-nav {
  height: 100vh;
  width: 100%;
  background-color: $black;
  overflow: auto;
  @include mq(md) {
    width: initial;
    height: inherit;
    background-color: transparent;
    overflow: initial;
  }
}

.p-header-nav__wrapper {
  height: inherit;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding-top: rem(196);
  padding-bottom: rem(34);
  @include mq(md) {
    padding: 0;
    flex-direction: row;
    justify-content: flex-start;
  }
}

.p-header-nav__main {
  width: 100%;
  @include mq(md) {
    height: inherit;
  }
}

.p-header-nav__sub {
  margin-top: rem(50);
  @include mq(md) {
    margin-top: 0;
  }
}

.p-header-nav__items {
  @include mq(md) {
    display: flex;
    height: 100%;
    align-items: center;
  }
}

.p-header-nav__item:nth-child(n + 2) {
  margin-top: rem(52);
  @include mq(md) {
    margin-top: 0;
  }
}

.p-header-nav__item {
  height: inherit;
  text-align: center;
  @include mq(md) {
    text-align: left;
  }
}

.p-header-nav__item a {
  display: block;
  color: $white;
  font-weight: bold;
  font-size: rem(16);
  line-height: calc(19 / 16);
  text-transform: uppercase;
  @include mq(md) {
    padding: 0 rem(29);
    height: inherit;
    display: flex;
    align-items: center;
    position: relative;
  }
}

.p-header-nav__item a::after {
  @include mq(md) {
    content: "";
    width: calc(100% - #{rem(29)} * 2);
    height: 2px;
    background-color: $white;
    display: inline-block;
    position: absolute;
    bottom: rem(30);
    transform: scaleX(0);
    transition: all 0.3s;
  }
}

.p-header-nav__item a:hover::after {
  @include mq(md) {
    transform: scaleX(1);
  }
}
