@use "global" as *;

// .p-top-recruit {}

.p-top-recruit__contents {
  display: flex;
  flex-direction: column;
  background-color: #d7d7d7;
  @include mq(md) {
    flex-direction: row;
  }
}

.p-top-recruit__inner {
  width: 100%;
  @include mq(md) {
    max-width: $innerWidth + $padding-pc * 2;
    padding-right: rem(strip-unit($padding-pc));
    padding-left: rem(strip-unit($padding-pc));
    margin-right: auto;
    margin-left: auto;
  }
}

.p-top-recruit__content {
  width: 100%;
  order: 1;
  padding: rem(60) rem(25);
  @include mq(md) {
    max-width: rem(445);
    order: 0;
    padding: rem(60) rem(20);
  }
}

.p-top-recruit__img {
  width: 100%;
  position: relative;
}

.p-top-recruit__img::before {
  content: "";
  display: block;
  padding-top: calc(140 / 375 * 100%);
  @include mq(md) {
    padding-top: calc(332 / 665 * 100%);
  }
}

.p-top-recruit__img img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}
