@use "global" as *;

.c-bread-crumbs {
  background-color: #e0e0e0;
}

.c-bread-crumbs__contents {
  display: flex;
  align-items: center;
  padding: rem(6) 0;
  @include mq(md) {
    padding: rem(15) 0;
  }
}

.c-bread-crumbs__contents span {
  display: inline-block;
  font-size: rem(11);
  line-height: calc(14 / 11);
  color: $black;
  @include mq(md) {
    font-size: rem(12);
    line-height: calc(15 / 12);
  }
}

.c-bread-crumbs__contents span a {
  margin-right: rem(10);
}

.c-bread-crumbs__contents span:not(:first-child) {
  margin-left: rem(10);
}
