@use "global" as *;

.p-info {
  padding: rem(30) 0;
  @include mq(md) {
    padding: rem(60) 0;
  }
}

.p-info__inner {
  width: 100%;
  padding-right: rem(strip-unit($padding-sp));
  padding-left: rem(strip-unit($padding-sp));
  margin-right: auto;
  margin-left: auto;
  @include mq(md) {
    max-width: $innerWidth + $padding-pc * 2;
    padding-right: rem(strip-unit($padding-pc));
    padding-left: rem(strip-unit($padding-pc));
  }
}

.p-info__content {
  margin-top: rem(20);
  @include mq() {
    margin-top: rem(35);
  }
}

.p-info__title {
  font-size: rem(24);
  line-height: 1;
  font-weight: bold;
  color: $black;
  text-align: center;
  @include mq(md) {
    font-size: rem(32);
  }
}

