.p-info__content-item {
  display: flex;
  flex-direction: column;
  align-items: baseline;
  color: $black;
  border-bottom: 1px solid #dddddd;
  padding: rem(20) 0;
  @include mq() {
    flex-direction: row;
    padding: rem(30) 0;
  }
}

.p-info__content-item dt {
  width: 100%;
  font-family: $font-sub;
  font-size: rem(16);
  line-height: 1;
  font-weight: 600;
  @include mq() {
    max-width: calc(#{rem(175)} + #{rem(30)});
    padding-left: rem(30);
  }
}

.p-info__content-item dd {
  width: 100%;
  font-size: rem(16);
  font-weight: 400;
  line-height: calc(19 / 16);
  margin-top: rem(10);
  @include mq(md) {
    margin-top: 0;
    padding-right: rem(30);
  }
}

.p-info__btn {
  text-align: center;
  margin-top: rem(40);
  @include mq(md) {
    margin-top: rem(50);
  }
}

.p-info__btn span {
  display: block;
  font-size: rem(12);
  line-height: 1;
  font-weight: 400;
  color: $black;
  margin-top: rem(5);
}
