@use "global" as *;

.p-contact {
  margin-bottom: rem(40);
  @include mq(md) {
    margin-bottom: rem(70);
  }
}

.p-contact__text {
  font-family: $font-sub;
  font-size: rem(14);
  line-height: rem(23);
  font-weight: 300;
  color: $black;
  text-align: center;
  margin-top: rem(40);
  @include mq(md) {
    line-height: 1;
    margin-top: rem(100);
  }
}

.p-contact__form {
  background-color: #f7f7f7;
  padding: rem(30);
  margin-top: rem(30);
  @include mq(md) {
    padding: rem(90);
    margin-top: rem(40);
  }
}

// Snow Monkey Forms
.smf-item:not(:first-child) {
  margin-top: rem(32);
}

.smf-item:not(:first-child) .smf-item__col {
  margin-top: rem(15);
}

.smf-item__col {
  display: flex;
  flex-wrap: wrap;
}

.smf-item__col,
.smf-item__controls,
.smf-text-control,
.smf-textarea-control {
  width: 100%;
}

.smf-text-control__control,
.smf-textarea-control__control,
.smf-select-control__control {
  width: 100%;
}

.smf01 .smf-item__description,
.smf02 .smf-item__description,
.smf03 .smf-item__description,
.smf04 .smf-item__description,
.smf06 .smf-item__description,
.smf07 .smf-item__description {
  font-family: $font-sub;
  font-size: rem(12);
  font-weight: 600;
  color: #a0a0a0;
  margin-top: 0;
  margin-left: rem(10);
  @include mq(md) {
    margin-left: rem(15);
  }
}

.smf05 .smf-item__description {
  font-family: $font-sub;
  font-size: rem(12);
  font-weight: 300;
  color: $black;
  margin-top: 0;
  margin-left: rem(5);
}

.smf-form input {
  font-family: $font-sub;
  font-size: rem(14);
  line-height: 1;
  font-weight: 300;
  color: $black;
}

.smf-item:nth-child(2) .smf-text-control__control,
.smf-item:nth-child(3) .smf-text-control__control,
.smf-item:nth-child(4) .smf-text-control__control {
  max-width: rem(300) !important;
}

.smf-item:nth-child(5) .smf-text-control__control {
  max-width: rem(200) !important;
}

.smf-textarea-control__control {
  max-width: rem(600) !important;
}

.smf-select-control__control {
  min-width: rem(300);
}

// お問い合わせ種別
.smf-radio-cuttons-control {
  margin-top: rem(15);
}
.smf-radio-buttons-control__control {
  display: flex;
  flex-direction: column;
  row-gap: rem(15);
  @include mq(md) {
    flex-direction: row;
    column-gap: rem(35);
    margin-top: rem(10);
  }
}
.smf-form .smf-item .smf-radio-button-control__control {
  box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 2px;
  margin: 0;
}
.smf-radio-button-control__label {
  margin-left: rem(5);
}

// ボタン
.smf-action {
  text-align: center;
  // pointer-events: none;
}

.smf-action .smf-button-control__control {
  min-width: rem(200);
  font-size: rem(14);
  line-height: calc(18 / 14);
  font-weight: bold;
  color: $black;
  background: #a1a1a1;
  border-radius: rem(30);
  padding: rem(18) 0;
  margin-top: rem(50);
}
