@use "global" as *;

.p-footer {
  background-color: $black;
}

.p-footer__inner {
  width: 100%;
  padding-right: rem(30);
  padding-left: rem(30);
  margin-right: auto;
  margin-left: auto;
}

.p-footer__contents {
  padding: rem(60) 0;
}

.p-footer__list {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  gap: rem(20);
  @include mq(md) {
    justify-content: center;
  }
}

.p-footer__list-item a {
  font-size: rem(12);
  font-weight: bold;
  line-height: calc(15 / 12);
  color: $white;
  @include mq(md) {
    font-size: rem(14);
    line-height: calc(18 / 14);
    transition: 0.3s;
    &:hover {
      opacity: 0.7;
      cursor: pointer;
    }
  }
}

.p-footer__copyright {
  font-size: rem(10);
  line-height: calc(13 / 10);
  color: $white;
  padding-top: rem(30);
  text-align: center;
  @include mq(md) {
    padding-top: rem(22);
  }
}
