// ページネーション
.nav-links {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: rem(10);
  margin-top: rem(40);
}

.page-numbers {
  display: flex;
  justify-content: center;
  align-items: center;
  width: rem(45);
  height: rem(45);
  border-radius: 50%;
  flex-wrap: wrap;
  overflow: hidden;
  background: $black;
  font-size: rem(20);
  font-weight: bold;
  line-height: calc(24 / 20);
  color: $white;
  transition: all 0.3s;
}

.page-numbers.current {
  background: #a1a1a1;
  color: $black;
  pointer-events: none;
}

.page-numbers:not(.current):hover {
  background: #a1a1a1;
  color: $black;
}
